/* App.css */

body {
  font-family: 'Arial', sans-serif;
  background: linear-gradient(to right, #da50ff, #00f2fe);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}

.App {
  background: rgba(255, 255, 255, 0.8);
  padding: 20px 40px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  width: 100%;
  max-width: 400px;
}

h1 {
  font-size: 2em;
  margin-bottom: 20px;
  color: #333;
}

form {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

input {
  padding: 10px;
  font-size: 1em;
  border: 1px solid #ccc;
  border-radius: 5px 0 0 5px;
  outline: none;
  width: 70%;
}

button {
  padding: 10px;
  font-size: 1em;
  border: none;
  background: #4facfe;
  color: white;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  outline: none;
}

button:hover {
  background: #00f2fe;
}

.weather-info {
  margin-top: 20px;
  background: #f9f9f9;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.weather-info h2 {
  margin: 0;
  color: #333;
}

.weather-info p {
  margin: 10px 0;
  color: #555;
}

.error {
  color: red;
  margin-top: 10px;
}
